input.cta-button {
  border: none; }

/* Dik gedrukt met streep-pijltje achter de link */
.cta {
  color: #02379d;
  text-decoration: none;
  transition: transform .3s; }
  .cta:after {
    display: inline-block;
    position: absolute;
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e901";
    margin-left: 15px;
    color: #02379d;
    text-decoration: none;
    font-size: .6em;
    vertical-align: middle;
    transition: transform .2s;
    padding-top: 1.1em; }
  .cta:hover {
    color: #01256b; }
    .cta:hover:after {
      color: #01256b;
      transform: translateX(5px); }

/* Gekleurde button met witte link inclusief pijltje */
.cta-special {
  background: #02379d;
  color: white;
  padding: 15px 33px;
  border-radius: 1px;
  text-decoration: none;
  font-weight: 600;
  border: none;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 12px 33px 12px 57px;
  line-height: 1.7em;
  margin-top: 1em;
  position: relative; }
  .cta-special:hover {
    text-decoration: none !important;
    background-color: #01256b; }
  .cta-special:before {
    display: inline-block;
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e906";
    color: #fff;
    text-decoration: none;
    font-size: 0.87em;
    vertical-align: middle;
    transition: transform .1s;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    left: 24px;
    margin: 0; }
  .cta-special:hover {
    color: white; }
    .cta-special:hover:before {
      transform: translate(4px, -50%); }

/* Donkere tekst met oranje pijltje voor de link */
.cta-simple {
  text-decoration: none;
  display: inline-block;
  padding-left: 30px;
  line-height: 1.7em;
  font-weight: 800; }
  .cta-simple:before {
    display: inline-block;
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e906";
    margin-right: 17px;
    text-decoration: none;
    font-size: .85em;
    vertical-align: middle;
    transition: transform .1s;
    margin-left: -30px; }
  .cta-simple:hover {
    color: #01256b; }
    .cta-simple:hover:before {
      transform: translateX(4px); }

